<template>
<div class="w100" v-show="Visible">
  <!-- <transition name="modal-fade"> -->
    <div class="modal-backdrop" v-on:click="ClickOutside()">
    </div>
    <div class="modal" role="dialog" >
      <header class="modal-header" ref="titleModal" >
          Editando registro pluviómetro...
      </header>
      <section class="modal-body mT10" id="modalDescription" >

        <div class="face face-front oHidden">
          <div class="content aliCenter">
            <!-- <div class="TituloLogin B">Login</div> -->
            <div class="">
              <div class="field-wrapper">
                <input v-model="Valor" type="number" placeholder="Valor" ref="Valor" />
                <label>Valor</label>
              </div>
            </div>
            <div class="">
              <div class="field-wrapper">
                <input v-model="FechaMedicion" type="text" placeholder="Fecha medición" ref="FechaMedicion" />
                <label>Fecha medición</label>
              </div>
            </div>
          </div>
        </div>
      </section>
      <footer class="modal-footer">
          <button type="button" class="Boton BotonCancelar" @click="Hide()" >Cancelar</button>
          <button type="button" class="Boton BotonAceptar" @click="Editar()" >Aceptar</button>
      </footer>
    </div>
  <!-- </transition> -->

</div>
</template>
<script>
  import ManejoDatos from "../../Auxiliares/ManejoDatos.js";
  export default {
    name: 'ModalEditarRegistroPluviometro',
    data() {
      return {
        IdRegistro: 0,
        Valor: "",
        FechaMedicion: "",
        Visible: false
      };
    },
    mounted() {
    },
    methods: {
      ClickOutside(){
        this.Hide();
      },
      async Editar() {
        try {
          if (await this.EditarRegistroPluviometro()){
            this.LimpiarDatos();
            this.$parent.RefrescarInfo();
          }
          this.Hide();
        } catch (error) {
          console.error(error);
        }
      },
      async EditarRegistroPluviometro(){
        try {
          var _FechaAlta = this.FechaMedicion.ValidDate(this._Registro._FechaAlta);
          var _User = this.$parent.$parent.Datos._User;
          var _Registro = {
            Id: this._Registro._Id,
            Valor: this.Valor,
            FechaMedicion: _FechaAlta,
            UserID: _User._ID
          }
          var response = await ManejoDatos.UpdateRegistroPluviometro(_Registro, this.$parent.$parent);
          if(response){
            this.$parent.$parent.Notificacion("Registro actualizado!", "", "success", 3);
            return true;
          }
          this.$parent.$parent.Notificacion("Algo salió mal! Intenta más tarde.", "", "error", 3);
          return false;
        } catch (error) {
          this.$parent.$parent.Notificacion("Algo salió mal! Intenta más tarde." + error, "", "error", 3);
          return false;
        }
      },
      async Show(_Registro) {
        this.Visible = true;
        this._Registro = _Registro;
        let _this = this;
        _this.$refs.titleModal.innerText = "Editando registro pluviómetro ID: " + _Registro._Id;
        setTimeout(() => {
            _this.$refs.Valor.focus();
            _this.Valor = _Registro._Valor;
            _this.FechaMedicion = _Registro._FechaAlta;
        }, 50);
      },
      Hide() {
        this.Visible = false;
      },
      LimpiarDatos(){
        this.IdRegistro = 0;
        this.Valor = "";
      }
    },
  };
</script>
<style lang="scss" scoped>
    $AgroIdeas: #005625;
    $text-dark: #444;
    $navy-blue: #999;

    .face {
        width: 100%;
        padding: 0px 10px;
        box-sizing: border-box;

        .content {
            color: $text-dark;
            h2 {
            font-size: 1.2em;
            color: $AgroIdeas;
            }
            .field-wrapper {
            margin-top: 20px;
            position: relative;

            label {
                position: absolute;
                pointer-events: none;
                font-size: 0.85em;
                top: 40%;
                left: 0;
                transform: translateY(-5px);
                transition: all ease-in 0.25s;
                color: lighten($text-dark, 20%);
            }
            input,
            textarea {
                -webkit-appearance: none;
                appearance: none;
                &:focus {
                outline: none;
                }
            }
            input,
            textarea {
                width: 100%;
                border: none;
                background: transparent;
                line-height: 2em;
                border-bottom: 1px solid $AgroIdeas;
                color: $text-dark;

                &::-webkit-input-placeholder {
                opacity: 0;
                }
                &::-moz-placeholder {
                opacity: 0;
                }
                &:-ms-input-placeholder {
                opacity: 0;
                }
                &:-moz-placeholder {
                opacity: 0;
                }
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -35%;
                    color: $navy-blue;
                }
                }
            }
            textarea {
                resize: none;
                line-height: 1em;
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -25%;
                }
                }
            }
            }
        }
    }


    .modal-fade-enter, .modal-fade-leave-active {
        opacity: 0;
    }
    .modal-fade-enter-active, .modal-fade-leave-active {
        transition: opacity .2s ease
    }
    .modal-backdrop {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 11;
    }
    .modal {
        background: #FFFFFF;
        box-shadow: 0px 0px 10px #222;
        overflow-x: auto;
        display: flex;
        flex-direction: column;
        width: 95%;
        max-width: 500px;

        position: fixed;
        justify-content: center;
        z-index: 11;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .modal-header,
    .modal-footer {
        padding: 15px;
        display: flex;
        // border-top: 1px solid #eeeeee;
        justify-content: space-between;
    }
    .modal-header {
        border-bottom: 1px solid #eeeeee;
        font-weight: bold;
        background: rgba(0,0,0,0.8);
        color: #CCC;
    }
    .modal-footer {
        margin-top:20px;
    }
    .Boton{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        cursor: pointer;
        width: 100px;
        line-height: 2em;
        color: #fff;
        border: 1px solid #000;
        border-radius: 3px;
        padding: 5px;
        &:hover {
            opacity: 0.9;
        }
        &:active {
            transform: scale(0.96);
        }
    }
    .BotonCancelar {
        background: #999;
    }
    .BotonAceptar {
        background: $AgroIdeas;
    }
</style>
